@import url('https://fonts.googleapis.com/css2?family=Jost&display=swap');
*{
  margin: 0;
  padding: 0;
  font-family: 'Jost', sans-serif;
}
.App{
  width: 320px;
  height: 500px;
  border: 10px solid rgba(0,0,0,0.5);
  position: relative;
  left: calc(50vw - 170px);
  margin-top:0px;
  box-shadow: 0px 0px 20px 5px #000;
  -webkit-box-reflect: below 30px linear-gradient(to bottom, rgba(0,0,0,0.0), rgba(0,0,0,0.8));
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  overflow: hidden;
  background-image:linear-gradient(-135deg,rgba(67, 14, 151,0.7),rgba(23, 23, 23,0.4)),url('./body.jpg');
}
.input{
  background:rgba(0,0,0,0.3);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
}
.fa-search{
  padding-right: 7px;
  font-size: 22px;
  color:rgba(255,255,255,0.6);
}
@keyframes dkji{
  50%{
    padding-right:30px;
  }
  100%{
    padding-right: 0px;
  }
}
.inputF{
  background:transparent;
  outline: none;
  height: 40px;
  width: 80%;
  border-radius: 10px;
  border: none;
  font-size: 20px;
  text-decoration: none;
  color: #62B6B7;
}
.cityName{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: 0px;
}
.cityName h2{
  color: #30e364;
  padding-left: 10px;
  font-size: 32px;
}
.fas{
  font-size: 38px;
  color: #30e364;
  animation: dkji 3s infinite;
}
.country{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  
}
body{
  min-height: 100vh;
  background: linear-gradient(0deg,#444852,#b6e0b9);
  animation: anim 1s  infinite ease;
}
@keyframes anim{
  0%{
  background: linear-gradient(0deg,#444852,#b6e0b9);
  }
  10%{
 background: linear-gradient(36deg,#444852,#b6e0b9);   
  }
  20%{
   background: linear-gradient(72deg,#444852,#b6e0b9); 
  }
  30%{
   background: linear-gradient(108deg,#444852,#b6e0b9); 
  }
  40%{
   background: linear-gradient(144deg,#444852,#b6e0b9); 
  }
  50%{
  background: linear-gradient(180deg,#444852,#b6e0b9);  
  }
  60%{
   background: linear-gradient(216deg,#444852,#b6e0b9); 
  }
  70%{
   background: linear-gradient(252deg,#444852,#b6e0b9); 
  }
  80%{
  background: linear-gradient(288deg,#444852,#b6e0b9);  
  }
  90%{
   background: linear-gradient(324deg,#444852,#b6e0b9); 
  }
  100%{
   background: linear-gradient(360deg,#444852,#b6e0b9); 
  }
}
.info{
  display: flex;
  flex-direction: column;
}
.info h2{
  text-align: center;
  padding: 10px;
}
.data{
  display: flex;
  width: 100%;
  align-items: center;
}
h4{
  color:rgba(255,255,255,0.5);
}
p{
  color:#74b587;
}
h2{
  color: #30e364;
}
@media only screen and(max-width:600px){
  .App{
    display: none;
  }
  body{
    display: flex;
    justify-content: center;
    align-items: center;
  }
}